<template>
  <swiper v-for="value in list" :key="value" class="flex flex-row justify-between"  :modules="modules" :slides-per-view="5" :space-between="20" :breakpoints="{500:{slidesPerView:3,spaceBetween:20},768:{slidesPerView:4,spaceBetween: 20},1024:{slidesPerView:5,spaceBetween:20}}" :navigation="true">
    <swiper-slide v-for="items in value.news" :key="items" class="flex flex-row justify-between">
      <div class="flex flex-col space-y-1 h-48">
        <div class="h-32 w-full">
          <img @error="notShow" :src="`${items.news_image[0].url}`" alt="" class="w-full h-full">
        </div>
        <p class="text font-thin text-start my-1 text-slate-900 sm:text-xs" :style="{fontSize:sizeTitle+'px'}">{{items.title}}</p>
        <div class="flex flex-row align-center justify-between w-full">
          <div><span class="font-thin text-start my-1 text-slate-900 sm:text-xs" :style="{fontSize:sizeTitle+'px'}">4 روز قبل</span></div>
          <div class="flex flex-row">
            <div>
              <font-awesome-icon :icon="['fas','comment']" class="font-thin text-start text-slate-800 sm:text-xs"></font-awesome-icon>
              <span class="font-thin text-start my-1 text-slate-900 sm:text-xs" :style="{fontSize:size+'px'}">{{items.news_comment_count}}</span>
            </div>
            <div>
              <font-awesome-icon :icon="['fas','eye']" class="font-thin text-start text-slate-800 sm:text-xs"></font-awesome-icon>
              <span class="font-thin text-start my-1 text-slate-900 sm:text-xs" :style="{fontSize:size+'px'}">{{items.visit}}</span>
            </div>
          </div>
        </div>
      </div>
    </swiper-slide>
  </swiper>
</template>

<script>
import {Swiper,SwiperSlide} from 'swiper/vue'
import SwiperCore,{Navigation} from "swiper";
SwiperCore.use([Navigation])
import 'swiper/css'
import 'swiper/css/navigation'
import{FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {ref} from "vue";
export default {
  name: "NostalogyBase",
  props:{
    list:{
      type:Array,
      required:true,
      default:null
    }
  },
  components:{Swiper,SwiperSlide,FontAwesomeIcon},
  setup(){
    const size=ref(12)
    const sizeTitle=ref(12)
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_default_news_image.png')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
      size,
      sizeTitle,
      notFound,
      notShow,
      modules:[Navigation]
    }
  }
}
</script>

